<template>
    <div class="environment">
        <Row style="height: 100%;">
            <i-col span="24" class="box">
                <i-col span="8" class="box-l" ref="centre">
                    <i-col span="24" class="box-centre">
                        <i-Circle :percent="percent" stroke-color="#4d6dff" :size="size">
                            <span class="box-centre-shu">25</span>°C
                        </i-Circle>
                        <div class="box-centre-zi">温度</div>
                    </i-col>
                </i-col>
                <i-col span="8" class="box-c" ref="centre">
                    <i-col span="24" class="box-centre">
                        <i-Circle :percent="percent" stroke-color="#00e2ff" :size="size">
                            <span class="box-centre-shu">24</span>%
                        </i-Circle>
                        <div class="box-centre-zi">湿度</div>
                    </i-col>
                </i-col>
                <i-col span="8" class="box-r" ref="centre">
                    <i-col span="24" class="box-centre">
                        <i-Circle :percent="percent" stroke-color="#c37adc" :size="size">
                            <span class="box-centre-shu">2.6</span>
                        </i-Circle>
                        <div class="box-centre-zi">PM值</div>
                    </i-col>
                </i-col>
            </i-col>
        </Row>
        <Spin size="large" fix v-if="spinShow"></Spin>
    </div>
</template>

<script>
export default {
  name: 'environment',
  data () {
    return {
        size: 0,
        spinShow: false,
        percent: 0
    }
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
        console.log(this.$refs.centre);
        this.percent = 0
        this.spinShow = true
        setTimeout(() => {
            this.percent = 100
            this.spinShow = false
        }, 2000);
        let clientHeight = this.$refs.centre.$el.clientHeight
        let clientWidth = this.$refs.centre.$el.clientWidth
        if (clientHeight >= clientWidth) {
            this.size = this.$refs.centre.$el.clientWidth - 20
        } else {
            this.size = this.$refs.centre.$el.clientHeight - 80
        }
    }
  },
}
</script>

<style scoped lang="less">
.environment {
    height: 100%;
    .box {
        height: 100%;
        .box-l,.box-r,.box-c {
            height: 100%;
            position: relative;
        }
        .box-centre {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
            text-align: center;
            .box-centre-shu {
                font-size: 18px;
            }
            .box-centre-zi {
                text-align: center;
            }
        }
    }
}
</style>



