<template>
  <div class="indoor">
      <Row style="height: 100%">
          <i-col span="24" class="indoor-bot">
              <div id="indoor" style="height: 100%"></div>
          </i-col>
      </Row>
      <Spin size="large" fix v-if="spinShow"></Spin>
  </div>
</template>

<script>
export default {
  name: 'indoor',
  data () {
    return {
        Radio: '今日',
        spinShow: false
    }
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      this.spinShow = true
      setTimeout(() => {
        // 基于准备好的dom，初始化echarts实例
        let myChart = this.$echarts.init(document.getElementById('indoor'))
        // 绘制图表
        let option = {
                    tooltip : {
                        trigger: 'axis'
                    },
                    color:['#3498ff','#f668a9','#ffc400'],
                    legend: {
                        data:['温度','湿度','光照强度'],
                        textStyle: {
                            color: '#fff'
                        }
                    },
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    xAxis : [
                        {
                            type : 'category',
                            data : ['2点', '4点', '6点', '8点', '10点', '12点', '14点', '16点', '18点', '20点', '22点', '24点'],
                            axisTick: {
                                alignWithLabel: true
                            },
                            axisLabel: {
                                show: true,
                                textStyle: {
                                    color: '#fff'
                                }
                            }
                        }
                    ],
                    yAxis : {
                        type: 'value',
                        axisLabel: {
                            formatter: '{value}',
                            show: true,
                            textStyle: {
                                color: '#fff'
                            }
                        }
                    },
                    series : [
                        {
                            name:'温度',
                            type:'line',
                            data:[50, 50, 55, 60, 70, 80, 50, 50, 55, 60, 70, 80]
                        },
                        {
                            name:'湿度',
                            type:'line',
                            data:[60, 70, 85, 90, 80, 70, 60, 70, 85, 90, 80, 70]
                        },
                        {
                            name:'光照强度',
                            type:'line',
                            data:[40, 30, 20, 50, 60, 30, 40, 30, 20, 50, 60, 30]
                        }
                    ]
        };
        myChart.setOption(option);
        this.spinShow = false
      }, 2000);
    }
  },
}
</script>

<style scoped lang="less">
.indoor {
    height: 100%;
    .indoor-bot {
        padding-top: 15px;
        height: 100%;
    }
}
</style>



