<template>
  <div class="energy">
      <Row style="height: 100%">
          <i-col span="24" class="energy-top">
              <i-col span="12" class="energy-top-l">
                    <RadioGroup v-model="Radio" type="button" size="small" class="energy-group">
                        <Radio label="今日"></Radio>
                        <Radio label="本月"></Radio>
                    </RadioGroup>
              </i-col>
              <i-col span="12" class="energy-top-r">用电(度): <span>183</span></i-col>
          </i-col>
          <i-col span="24" class="energy-bot">
              <div id="energy" style="height: 100%"></div>
          </i-col>
      </Row>
      <Spin size="large" fix v-if="spinShow"></Spin>
  </div>
</template>

<script>
export default {
  name: 'energy',
  data () {
    return {
        Radio: '今日',
        spinShow: false
    }
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
        this.spinShow = true
      setTimeout(() => {
        // 基于准备好的dom，初始化echarts实例
        let myChart = this.$echarts.init(document.getElementById('energy'))
        // 绘制图表
        let option = {
            tooltip : {
                trigger: 'axis',
                axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                    type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                },
                // formatter: function (params) {
                //     // do some thing
                //     // return  “名称：” +params.name
                //     console.log(params);
                //     // return  '名称：' +params.name
                // }
            },
            color:['#0090ff','#7760f6','#00edff','#19cafd','#52cbdd'],
            legend: {
                data:['空调','照明','插座','其他', '总量'],
                textStyle: {
                    color: '#fff'
                }
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            xAxis : [
                {
                    type : 'category',
                    data : ['4点','8点','12点','16点','20点','24点'],
                    axisTick: {
                        alignWithLabel: true
                    },
                    axisLabel: {
                        show: true,
                        textStyle: {
                            color: '#fff'
                        }
                    }
                }
            ],
            yAxis : [
                {
                    type: 'value',
                    axisLabel: {
                        formatter: '{value}',
                        show: true,
                        textStyle: {
                            color: '#fff'
                        }
                    }
                }
            ],
            series : [
                {
                    name:'空调',
                    type:'bar',
                    stack: '广告',
                    data:[30, 35, 40, 50, 35, 20]
                },
                {
                    name:'照明',
                    type:'bar',
                    stack: '广告',
                    data:[35, 40, 50, 60, 75, 80]
                },
                {
                    name:'插座',
                    type:'bar',
                    stack: '广告',
                    data:[40, 30, 45, 56, 66, 70]
                },
                {
                    name:'其他',
                    type:'bar',
                    stack: '广告',
                    data:[50, 50, 55, 60, 70, 80]
                },
                {
                    name:'总量',
                    type:'line',
                    data:[160, 170, 200, 250, 260, 270]
                }
            ]
        };
        myChart.setOption(option);
        this.spinShow = false
      }, 2000);
    }
  },
}
</script>

<style scoped lang="less">
.energy {
    height: 100%;
    .energy-top {
        position: absolute;
        left: 0;
        top: 0;
    }
    .energy-bot {
        padding-top: 35px;
        height: 100%;
    }
}
.energy-top-l {
    position: relative;
    height: 33px;
    .energy-group {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        color: #fff
    }
    .ivu-radio-group-button .ivu-radio-wrapper:after, .ivu-radio-group-button .ivu-radio-wrapper:before {
        width: 0;
    }
    .ivu-radio-group-button  {
        .ivu-radio-wrapper-checked {
            background-color: #0b1035;
        }
    }
    .ivu-radio-group-button {
        .ivu-radio-wrapper {
            background-color: #0b1035;
            color: #fff;
            border: 1px solid #242e80;
        }
    }
    .ivu-radio-group-button {
        .ivu-radio-wrapper-checked {
            color: #fff;
            border: 0 solid #000;
            background-color: #242e80;
        }
    }
    .ivu-radio-group-button {
        .ivu-radio-wrapper-checked {
            box-shadow: none;
        }
    }
}
.energy-top-r{
    text-align: right;
    color: #4c78ea;
    span {
        font-size: 22px;
    }
}
</style>



