<template>
  <div class="personnel">
      <Row style="height: 100%;">
          <i-col span="24" style="height: 180px;z-index: 999;">
              <div id="container" style="height: 100%"></div>
          </i-col>
          <i-col span="24" class="Pro" v-show="!spinShow">
              <i-col span="7" class="Pro-l">
                  进出总人数
              </i-col>
              <i-col span="14" class="Pro-z">
                  <Progress :percent="100" hide-info />
              </i-col>
              <i-col span="3" class="Pro-r">
                  80
              </i-col>
              <i-col span="7" class="Pro-l">
                  进入人数
              </i-col>
              <i-col span="14" class="Pro-z">
                  <Progress :percent="20" hide-info />
              </i-col>
              <i-col span="3" class="Pro-r">
                  20
              </i-col>
              <i-col span="7" class="Pro-l">
                  出去人数
              </i-col>
              <i-col span="14" class="Pro-z">
                  <Progress :percent="60" hide-info />
              </i-col>
              <i-col span="3" class="Pro-r">
                  60
              </i-col>
          </i-col>
      </Row>
      <Spin size="large" fix v-if="spinShow"></Spin>
  </div>
</template>

<script>
export default {
  name: 'personnel',
  data () {
    return {
        spinShow: false
    }
  },
  mounted() {
    this.drawLine();
  },
  methods: {
        drawLine() {
            this.spinShow = true
            setTimeout(() => {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('container'))
                // 绘制图表
                let option = {
                    tooltip: {
                        trigger: 'item',
                        // formatter: "{a} <br/>{b}: {c} ({d}%)"
                        formatter: "{a} <br/>{b}: {c} 人"
                    },
                    // legend: {
                    //     orient: 'vertical',
                    //     x: 'left',
                    //     data:['直接访问','邮件营销','联盟广告','视频广告','搜索引擎']
                    // },
                    color:['#0dbbe0','#4352d4'],
                    series: [
                        {
                            name:'访问来源',
                            type:'pie',
                            radius: ['30%', '80%'],
                            avoidLabelOverlap: true,
                            label: {
                                normal: {
                                    show: true,
                                },
                                emphasis: {
                                    show: true,
                                    textStyle: {
                                        fontSize: '18',
                                        fontWeight: 'bold'
                                    }
                                }
                            },
                            labelLine: {
                                normal: {
                                    show: true
                                },
                                emphasis: {
                                    show: true
                                }
                            },
                            data:[
                                {value:20, name:'进25%'},
                                {value:60, name:'出75%'}
                            ]
                        }
                    ]
                };
                myChart.setOption(option);
                this.spinShow = false
            }, 2000);
        },
    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
    .personnel {
        height: 100%;
    }
    .Pro {
        border: 1px solid #1c3872;
        padding: 10px;
        position: absolute;
        bottom: 0;
        left: 0;
        .Pro-l {
            // padding-right: 16px;
            text-align: center;
        }
        .Pro-z {
            /deep/ .ivu-progress-bg,.ivu-progress-success-bg {
                background-color: #3e9dca;
            }
            /deep/ .ivu-progress-inner {
                background-color: #2f4b7f;
            }
        }
        .Pro-r {
            // padding-left: 10px;
            text-align: center;
        }
    }
</style>



