<template>
  <div class="index">
      <Row style="height: 100%;">
          <i-col span="24" class="index-top">
                <i-col span="6" class="jc-rgba">
                    1
                </i-col>
                <i-col span="12" class="index-c">
                    老年活动中心可视化平台
                </i-col>
                <i-col span="6" class="index-r">
                    2019年11月12日 18:06 星期二 晴 太阳
                </i-col>
          </i-col>
          <i-col span="24" class="index-con">
                <i-col span="6" style="height: 100%;" class="index-left">
                    <i-col span="24" class="left-top" style="margin-bottom: 30px;height: 300px;">
                        <span class="left-top-t">进出人员统计</span>
                        <personnel></personnel>
                        <i class="jiao-t-l"></i>
                        <i class="jiao-t-r"></i>
                        <i class="jiao-b-l"></i>
                        <i class="jiao-b-r"></i>
                    </i-col>
                    <i-col span="24" class="left-b">
                        <i-col span="24" class="left-bot">
                            <span class="left-bot-t">能源消耗数据</span>
                            <energy></energy>
                            <i class="jiao-t-l"></i>
                            <i class="jiao-t-r"></i>
                            <i class="jiao-b-l"></i>
                            <i class="jiao-b-r"></i>
                        </i-col>
                    </i-col>
                    
                </i-col>
                <i-col span="12" class="index-mid">
                    <i-col span="24" class="mid-box">
                        <i-col span="24" class="mid-top">
                            <i-col span="24" class="mid-top-box">
                                <maps></maps>
                                <i class="jiao-t-l"></i>
                                <i class="jiao-t-r"></i>
                                <i class="jiao-b-l"></i>
                                <i class="jiao-b-r"></i>
                            </i-col>
                        </i-col>
                        <i-col span="24" class="mid-bot">
                            <i-col span="24" class="bot">
                                <span class="bot-t">室内环境数据</span>
                                <indoor></indoor>
                                <i class="jiao-t-l"></i>
                                <i class="jiao-t-r"></i>
                                <i class="jiao-b-l"></i>
                                <i class="jiao-b-r"></i>
                            </i-col>
                        </i-col>
                    </i-col>
                </i-col>
                <i-col span="6" class="index-right">
                    <i-col span="24" class="right-top">
                        <span class="top-t">室内环境数据</span>
                        <i-col span="24" class="right-box">
                            <environment></environment>
                        </i-col>
                        <i class="jiao-t-l"></i>
                        <i class="jiao-t-r"></i>
                        <i class="jiao-b-l"></i>
                        <i class="jiao-b-r"></i>
                    </i-col>
                    <i-col span="24" class="right-bottom">
                        <i-col span="24" class="bottom-box">
                            <span class="bottom-t">每人活动清单</span>
                            <i-col span="24" class="box">
                                <activity></activity>
                            </i-col>
                            <i class="jiao-t-l"></i>
                            <i class="jiao-t-r"></i>
                            <i class="jiao-b-l"></i>
                            <i class="jiao-b-r"></i>
                        </i-col>
                    </i-col>
                </i-col>
          </i-col>
      </Row>
  </div>
</template>

<script>
import personnel from '../components/personnel/personnel'
import energy from '../components/energy/energy'
import indoor from '../components/indoor/indoor'
import environment from '../components/environment/environment'
import activity from '../components/activity/activity'
import maps from '../components/maps/maps'
export default {
  name: 'index',
  components: {personnel, energy, indoor, environment, activity, maps},
//   components: {personnel},
  data () {
    return {
    }
  }
}
</script>

<style scoped lang="less">
    @framecolor: #1c3872;
    .index {
        .jiao-t-l,.jiao-t-r,.jiao-b-l,.jiao-b-r {
            display: block;
            width: 20px;
            height: 20px;
            position: absolute;
            z-index: 9;
        }
        .jiao-t-l {
            top: -2px;
            left: -2px;
            border-top: 5px solid #959db5;
            border-left: 5px solid #959db5;
        }
        .jiao-t-r {
            top: -2px;
            right: -2px;
            border-top: 5px solid #959db5;
            border-right: 5px solid #959db5;
        }
        .jiao-b-l {
            left: -2px;
            bottom: -2px;
            border-bottom: 5px solid #959db5;
            border-left: 5px solid #959db5;
        }
        .jiao-b-r {
            right: -2px;
            bottom: -2px;
            border-bottom: 5px solid #959db5;
            border-right: 5px solid #959db5;
        }
        background-color: #0b1035;
        height: 100%;
        color: #fff;
        padding: 20px;
        /deep/ .ivu-spin-fix {
            background-color: rgba(0,0,0,0);
        }
        /deep/ .ivu-spin-dot {
            background-color: #fff;
        }
        .index-top {
            height: 50px;
            line-height: 50px;
            position: absolute;
            top: 0;
            left: 0;
            .index-c {
                text-align: center;
                font-size: 28px;
                font-weight: 600;
            }
            .index-r {
                text-align: right;
            }
        }
        .index-con {
            padding-top: 50px;
            height: 100%;
            .index-mid {
                height: 100%;
                padding: 0 30px;
                .mid-box {
                    position: relative;
                    height: 100%;
                }
                .mid-top {
                    height: 100%;
                    padding-bottom: 325px;
                    .mid-top-box {
                        height: 100%;
                        border: 1px solid @framecolor;
                    }
                }
                .mid-bot {
                    height: 300px;
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    .bot {
                        border: 1px solid @framecolor;
                        position: relative;
                        padding: 10px;
                        height: 100%;
                        .bot-t {
                            position: absolute;
                            left: 50%;
                            top: -10px;
                            background-color: #0b1035;
                            padding: 0 10px;
                            transform: translateX(-50%);
                        }
                    }
                }
            }
            .index-right {
                height: 100%;
                .right-box {
                    height: 100%;
                }
                .right-top {
                    position: absolute;
                    border: 1px solid @framecolor;
                    height: 200px;
                    .top-t {
                        position: absolute;
                        left: 50%;
                        top: -10px;
                        background-color: #0b1035;
                        padding: 0 10px;
                        transform: translateX(-50%);
                    }
                }
                .right-bottom {
                    padding-top: 230px;
                    height: 100%;
                    .bottom-box {
                        position: relative;
                        height: 100%;
                        border: 1px solid @framecolor;
                        .box {
                            height: 100%;
                        }
                        .bottom-t {
                            position: absolute;
                            left: 50%;
                            top: -10px;
                            background-color: #0b1035;
                            padding: 0 10px;
                            transform: translateX(-50%);
                        }
                    }
                    
                }
            }
        }
        .index-left {
            position: relative;
            .left-top {
                border: 1px solid @framecolor;
                position: relative;
                padding: 10px;
                position: absolute;
                top: 0;
                left: 0;
                .left-top-t {
                    position: absolute;
                    left: 50%;
                    top: -10px;
                    background-color: #0b1035;
                    padding: 0 10px;
                    transform: translateX(-50%);
                }
            }
            .left-b {
                padding-top: 330px;
                height: 100%;
            }
            .left-bot {
                border: 1px solid @framecolor;
                position: relative;
                padding: 10px;
                height: 100%;
                .left-bot-t {
                    position: absolute;
                    left: 50%;
                    top: -10px;
                    background-color: #0b1035;
                    padding: 0 10px;
                    transform: translateX(-50%);
                }
            }
            
        }
        
    }
    
</style>



