<template>
  <div class="activity">
      <Row style="height: 100%;">
          <i-col span="24" class="top">
              <span class="top-t">设备开关</span>
              <div id="activity" style="height: 100%"></div>
          </i-col>
          <i-col span="24" class="bot" ref="act">
              <span class="bot-t">医疗机械使用</span>
              <div id="activity2" style="height: 100%"></div>
          </i-col>
      </Row>
      <Spin size="large" fix v-if="spinShow"></Spin>
  </div>
</template>
<script>
export default {
  name: 'activity',
  data () {
    return {
        spinShow: false
    }
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
        let clientWidth = this.$refs.act.$el.clientWidth / 5
        console.log(this.$refs.act.$el.clientWidth);
      this.spinShow = true
      setTimeout(() => {
        // 基于准备好的dom，初始化echarts实例
        let myChart = this.$echarts.init(document.getElementById('activity'))
        let myChart2 = this.$echarts.init(document.getElementById('activity2'))
        // 绘制图表
        let option = {
            grid: {
                left: '3%',
                right: '7%',
                bottom: '3%',
                containLabel: true
            },
            // tooltip : {
            //         trigger: 'item',
            //         formatter: "{a} <br/>{b} : {c} 次 ({d}%)"
            //     },
            tooltip : {
                trigger: 'axis',
                formatter: "{a} <br/>{a} : {c} 次",
                showDelay : 0,
                axisPointer:{
                    show: true,
                    type : 'cross',
                    lineStyle: {
                        type : 'dashed',
                        width : 1
                    }
                }
            },
            color:['#d15f6d','#5fb8ca'],
            legend: {
                data: ['电视','室内照明'],
                left: 'center',
                textStyle: {
                    color: '#fff'
                }
            },
            xAxis : [
                {
                    type : 'value',
                    scale:true,
                    axisLabel : {
                        formatter: '{value} 点',
                        textStyle: {
                            color: '#fff'
                        }
                    },
                    splitLine: {
                        show: false
                    }
                }
            ],
            yAxis : [
                {
                    type : 'value',
                    scale:true,
                    axisLabel : {
                        formatter: '{value} h',
                        textStyle: {
                            color: '#fff'
                        }
                    },
                    splitLine: {
                        show: false
                    }
                }
            ],
            series : [
                {
                    name:'电视',
                    type:'scatter',
                    data: [
                        [4, 1.5], [8, 2], [12, 5], [16, 3], [20,4]
                    ]
                },
                {
                    name:'室内照明',
                    type:'scatter',
                    data: [
                        [8, 3], [14, 5], [18, 6], [19, 3], [23, 2],
                    ]
                }
            ]
        };
        let option2 = {
                tooltip : {
                    trigger: 'item',
                    formatter: "{a} <br/>{b} : {c} 次 ({d}%)"
                },
                color:['#3aa3d8','#38c6e7','#a0e6b9','#feda66','#fe9e82','#fa7194','#e6bcf2','#8378e7'],
                series : [
                    {
                        name:'面积模式',
                        type:'pie',
                        radius : [10, clientWidth],
                        center : ['50%', '50%'],
                        roseType : 'area',
                        data:[
                            {value:10, name:'血糖仪'},
                            {value:5, name:'助听器'},
                            {value:15, name:'治疗仪'},
                            {value:25, name:'轮椅'},
                            {value:20, name:'氧机'},
                            {value:35, name:'按摩器'},
                            {value:30, name:'助行器'},
                            {value:40, name:'血压计'}
                        ]
                    }
                ]
            };
        myChart.setOption(option);
        myChart2.setOption(option2);
        this.spinShow = false
      }, 2000);
    }
  },
}
</script>

<style scoped lang="less">
.activity {
    height: 100%;
    .top,.bot {
        height: 50%;
        position: relative;
    }
    .top {
        padding-top: 15px;
        .top-t {
            position: absolute;
            left: 10px;
            top: 10px;
            // background-color: #fff;
            background: linear-gradient(to right,#232d7e ,#0b1035);
            padding: 0 10px;
            border-radius: 20px;
        }
    }
    .bot {
        .bot-t {
            position: absolute;
            left: 10px;
            top: 10px;
            // background-color: #fff;
            background: linear-gradient(to right,#232d7e ,#0b1035);
            padding: 0 10px;
            border-radius: 20px;
        }
    }
}
</style>



